import DicodingStoryApi from '../../data/api';
import { saveStories, getAllStories } from '../../idb';

export default class HomePresenter {
  #view;
  #model;
  #page;
  #hasMoreData;
  #isLoading;
  #includeLocation;

  constructor({ view, model }) {
    this.#view = view;
    this.#model = model;
    this.#page = 1;
    this.#hasMoreData = true;
    this.#isLoading = false;
    this.#includeLocation = false;
  }

  async init() {
    try {
      this.#view.renderPage();
      this.#setupEventListeners();
      await this.#loadInitialStories();
    } catch (error) {
      console.error('Error in HomePresenter.init:', error);
      this.#view.showError('Error initializing page. Please try refreshing.');
    }
  }

  #setupEventListeners() {
    try {
      this.#view.setLoadMoreCallback(async () => {
        await this.#loadMoreStories();
      });

      this.#view.setLocationFilterCallback((isChecked) => {
        this.#includeLocation = isChecked;
        this.#page = 1;
        this.#hasMoreData = true;
        this.#loadInitialStories();
      });
    } catch (error) {
      console.error('Error setting up event listeners:', error);
    }
  }

  async #loadInitialStories() {
    if (this.#isLoading) return;

    this.#isLoading = true;
    this.#page = 1;

    try {
      this.#view.clearStories();
      this.#view.showLoading();

      const token = localStorage.getItem('token');
      if (!token) {
        this.#view.showLoginRequired();
        return;
      }

      console.log(
        'Fetching initial stories, page:',
        this.#page,
        'location:',
        this.#includeLocation ? 1 : 0
      );
      const response = await DicodingStoryApi.getAllStories({
        token,
        page: this.#page,
        location: this.#includeLocation ? 1 : 0,
      });

      if (response.error) {
        this.#view.showError(response.message || 'Gagal memuat cerita');
        return;
      }

      // Validate response structure
      if (!response || typeof response !== 'object') {
        console.error('Invalid response format:', response);
        this.#view.showError('Format respons server tidak valid');
        return;
      }

      const stories = response.listStory || [];
      const size = response.size || 10;

      console.log('Received stories:', stories.length);

      // Ensure stories is an array
      if (!Array.isArray(stories)) {
        console.error('Stories is not an array:', stories);
        this.#view.showError('Format data cerita tidak valid');
        return;
      }

      if (stories.length === 0) {
        this.#view.showEmptyState();
        this.#hasMoreData = false;
      } else {
        // Persist stories to IndexedDB for offline use
        await saveStories(stories);
        this.#view.renderStories(stories);
        this.#hasMoreData = stories.length === size;
        this.#view.updateLoadMoreButton(this.#hasMoreData);
      }
    } catch (error) {
      console.error('Error loading initial stories:', error);
      // Try to load from cache if online fetch fails
      const cachedStories = await getAllStories();
      if (cachedStories && cachedStories.length > 0) {
        this.#view.renderStories(cachedStories);
        this.#view.showOfflineMessage();
      } else {
        this.#view.showError(error.message || 'Gagal memuat cerita');
      }
    } finally {
      this.#isLoading = false;
      this.#view.hideLoading();
    }
  }

  async #loadMoreStories() {
    if (this.#isLoading || !this.#hasMoreData) return;

    this.#isLoading = true;
    this.#page += 1;

    try {
      this.#view.showLoadingMore();

      const token = localStorage.getItem('token');
      if (!token) {
        this.#view.showLoginRequired();
        return;
      }

      console.log(
        'Fetching more stories, page:',
        this.#page,
        'location:',
        this.#includeLocation ? 1 : 0
      );
      const response = await DicodingStoryApi.getAllStories({
        token,
        page: this.#page,
        location: this.#includeLocation ? 1 : 0,
      });

      if (response.error) {
        this.#view.showError(
          response.message || 'Gagal memuat cerita tambahan'
        );
        return;
      }

      // Validate response structure
      if (!response || typeof response !== 'object') {
        console.error('Invalid response format:', response);
        this.#view.showError('Format respons server tidak valid');
        return;
      }

      const stories = response.listStory || [];
      const size = response.size || 10;

      console.log('Received more stories:', stories.length);

      // Ensure stories is an array
      if (!Array.isArray(stories)) {
        console.error('Stories is not an array:', stories);
        this.#view.showError('Format data cerita tidak valid');
        return;
      }

      if (stories.length > 0) {
        // Persist stories to IndexedDB
        await saveStories(stories);
        this.#view.appendStories(stories);
        this.#hasMoreData = stories.length === size;
      } else {
        this.#hasMoreData = false;
      }

      this.#view.updateLoadMoreButton(this.#hasMoreData);
    } catch (error) {
      console.error('Error loading more stories:', error);
      this.#view.showError(error.message || 'Gagal memuat cerita tambahan');
    } finally {
      this.#isLoading = false;
      this.#view.hideLoadingMore();
    }
  }
}
